import { Badge, Image, Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { PaginationRequest, TableColumn } from "../../../../models/baseModel";
import { TaskProjectManageService } from "../../../../services/taskService";
import { MyBox } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../core/TableModel";
import TableComponent from "../../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../../core/UI_FormatComponent";

export default function IndexComponent() {
    const column = [
        new TableColumn({ title: "STT", key: "stt" }),
        new TableColumn({
            title: "QR Code", key: "qrCode", render: (row: any, data: any) => {
                return (
                    <Image src={TaskProjectManageService.GetQrCode(data.key)} width={100} height={100} />
                );
            }
        }),
        new TableColumn({
            title: "Tên", key: "name", render: (data: string, row: any) => {
                return <Link to={`/tools/tasks/projects/${row.key}`}>{data}</Link>
            }
        }),
        new TableColumn({ title: "Nội dung", key: "content" }),
        new TableColumn({
            title: "Tag", key: "tag", render: (data: any) => {
                const color = ["magenta", "red", "volcano", "orange", "gold", "lime", "green", "cyan", "blue", "geekblue", "purple"];
                return data.map((data: any, index: number) => {
                    return (
                        <Tag key={index} color={color[Math.floor(Math.random() * 11)]}>{data}</Tag>
                    );
                })
            }
        }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (data: any, row: any) => {
                let status: any;
                let text: string = row.statusText;

                if (row.statusIsNew) {
                    status = "warning";
                } else if (row.statusIsClosed) {
                    status = "error";
                } else {
                    status = "processing";
                }
                return <Badge status={status} text={text} />
            }
        }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (data: any) => {
                return (
                    <UIFormatDateTimeVNComponent value={data} />
                )
            }
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối cùng", key: "updatedAt", render: (data: any) => {
                return (
                    <UIFormatDateTimeVNComponent value={data} />
                )
            }
        }),
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const loadData = useCallback(() => {
        setIsLoading(true);
        var paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        TaskProjectManageService.GetList(paginationRequest, (data: any) => {
            var row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    key: data.key,
                    stt: index + 1,
                    name: data.name,
                    content: data.content,
                    tag: data.tag,
                    status: data.status,
                    statusText: data.statusText,
                    statusIsClosed: data.statusIsClosed,
                    statusIsNew: data.statusIsNew,
                    createdAt: data.createdAt,
                    updatedAt: data.updatedAt,
                };
            })
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    return (
        <MyBox
            title="Quản lý dự án"
            isLoading={isLoading}
            buttonList={() => (
                <Link to="/tools/tasks/projects/add">
                    <MyButton type="primary" value="Thêm dự án mới" />
                </Link>
            )}>

            <TableComponent
                scroll={500}
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </MyBox>
    );
}