import { MyAxios } from "../helper/myaxios";
import { PaginationRequest } from "../models/baseModel";

class TaskProjectManageService {
    static GetQrCode(key: string) {
        var url = `${MyAxios.BaseURL}/user/qrCode?data=taskManage_projectManage_${key}`;
        return url;
    }

    static GetLog(projectKey: string | undefined, paginationRequest: PaginationRequest, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects/${projectKey}/logs?${paginationRequest.generateQueryString()}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetClone(projectKey: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/duplicate`;
        return MyAxios.Get(url, then, exp, final);
    }

    static ProcessClone(projectKey: string | undefined, body: any, then?: any, exp?: any, final?: any) {
        var url = `tools/tasks/projects/${projectKey}/duplicate`;
        return MyAxios.Post(url, body, then, exp, final);
    }

    static GetList(paginationRequest: PaginationRequest, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects?${paginationRequest.generateQueryString()}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static GetDetail(key: string | undefined, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects/${key}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Search(q: string, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects/query?q=${q}`;
        return MyAxios.Get(url, then, exp, final);
    }

    static Add(body: any, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects`;
        console.log(body);

        return MyAxios.Post(url, body, then, exp, final);
    }

    static Edit(key: string | undefined, body: any, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects/${key}`;
        return MyAxios.Put(url, body, then, exp, final);
    }

    static Delete(key: string | undefined, timestamp: string, then?: any, exp?: any, final?: any) {
        var url = `v2/tools/taskManages/projects/${key}?timestamp=${encodeURIComponent(timestamp)}`;
        return MyAxios.Delete(url, then, exp, final);
    }
}

